.footer{
    width: 100%;
    height: 200px;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
}

.socialMedia svg{
    color: bisque;
    margin: 20px;
    font-size: 50px;
    cursor: pointer;
}

.socialMedia svg:hover{
    color: white;
    transition: .1s ease-in;
}
.footer p{
    color: wheat;
}